import { NextRequest, NextResponse } from "next/server"
import { getServerSession } from "next-auth"
import { authOptions } from "@/lib/auth"

/**
 * Custom endpoint for customer Google OAuth login
 * This endpoint bypasses NextAuth's signin page for a smoother UX
 */
export async function GET(request: NextRequest) {
  const { searchParams, origin } = new URL(request.url)
  const callbackUrl = searchParams.get("callbackUrl") || "http://localhost:5173/customer/dashboard"
  
  // Check if user is already signed in
  const session = await getServerSession(authOptions)
  if (session?.user?.role === "customer") {
    return NextResponse.redirect(callbackUrl)
  }
  
  // Redirect to NextAuth Google signin with callback
  const signinUrl = new URL("/api/auth/signin/google", origin)
  signinUrl.searchParams.set("callbackUrl", callbackUrl)
  
  return NextResponse.redirect(signinUrl)
}
